import React, { ReactNode } from "react";
import styled from "styled-components";

interface MainProps {
  children: ReactNode;
}

const InvTemplateBlock = styled.div`
  width: 420px;
  // height: 5000px;

  position: relative; /*         */
  background: white;
  box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.04);

  margin: 0 auto; /*     */

  margin-bottom: 32px;
  display: flex;
  flex-direction: column;
  color: rgb(137, 128, 120);
`;

const InvTemplate = ({ children }: MainProps) => {
  return <InvTemplateBlock>{children}</InvTemplateBlock>;
};

export default InvTemplate;
