import React from "react";
import tree from "../assets/img/tree.png";
import card from "../assets/img/card.png";
import styled from "styled-components";
import { phraseText, poemText } from "../constants/index";

const InvPhrase = () => {
  const InvPhraseBlock = styled.div`
    p {
      font-size: 13px;
    }
    .img__box {
      width: 100%;
      height: 100px;
      overflow: hidden;
      margin: 0 auto;
      text-align: center;

      .img_tree {
        width: 40px;
        height: 40px;
        object-fit: fill;
      }

      .img_card {
        width: 40px;
        height: 40px;
        object-fit: fill;
      }
    }

    .poem__box {
      padding-bottom: 60px;
      .poem {
        text-align: center;
        p {
          font-size: 15px;
        }
      }
    }

    .text__box {
      text-align: center;

      .desc {
        color: #cb8c8c;
      }

      .title {
        font-size: 16px;
        padding-bottom: 48px;
      }
      .text:nth-child(odd) {
        padding-bottom: 10px; /*     */
      }
    }
  `;
  return (
    <InvPhraseBlock>
      <div className="img__box">
        <img className="img_tree" alt="" src={tree} />
      </div>
      <div className="poem__box">
        {poemText.map((el, key) => (
          <div className="poem" key={key}>
            <p>{poemText[key]}</p>
          </div>
        ))}
      </div>
      <div className="text__box">
        <p className="desc">A celebration of our friendship</p>
        <p className="title">  </p>
        <div className="img__box">
          <img className="img_card" alt="" src={card} />
        </div>
        {phraseText.map((el, key) => (
          <div className="text" key={key}>
            <p>{phraseText[key]}</p>
          </div>
        ))}
      </div>
    </InvPhraseBlock>
  );
};

export default InvPhrase;
