import React from "react";
import styled from "styled-components";
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination } from "swiper/modules";
import img1 from "../assets/img/2022-1.jpg";
import img2 from "../assets/img/2022-2.jpg";
import img3 from "../assets/img/2021-1.jpg";
import img4 from "../assets/img/2021-2.jpg";
import img5 from "../assets/img/2021-3.jpg";
import img6 from "../assets/img/2020-1.jpg";

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/scrollbar";

const InvGallery = () => {
  const InvGalleryBlock = styled.div`
    .title {
      text-align: center;
      padding-top: 60px;
      padding-bottom: 24px;
      font-size: 32px;
      font-family: "PinyonScript";
    }

    .swiper {
      width: 100%;
      height: 600px;
    }

    .swiper-slide {
      width: 100%;
      text-align: center;
      font-size: 18px;
      // background: #fff;
      border-radius: 30px;

      /* Center slide text vertically */
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .swiper-slide img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    .swiper-pagination {
      bottom: 0;
    }
  `;
  return (
    <InvGalleryBlock>
      <div className="title">Gallery</div>
      <Swiper
        grabCursor={true}
        slidesPerView={1}
        spaceBetween={30}
        pagination={{
          clickable: true,
        }}
        modules={[Pagination]}
        className="mySwiper"
      >
        <SwiperSlide>
          <img src={img1} alt="" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={img2} alt="" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={img3} alt="" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={img4} alt="" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={img5} alt="" />
        </SwiperSlide>
        <SwiperSlide>
          <img src={img6} alt="" />
        </SwiperSlide>
      </Swiper>
    </InvGalleryBlock>
  );
};

export default InvGallery;
