import React from "react";
import styled from "styled-components";
import VolumeOffIcon from "@mui/icons-material/VolumeOff";
import { nameText } from "../constants/index";
import party from "../assets/img/party.jpg";

const InvCoverBlock = styled.div`
  padding-top: 48px;
  padding-left: 32px;
  padding-right: 32px;
  padding-bottom: 48px;

  p {
    text-align: center;
    font-weight: 100;
  }

  .intro__box {
    padding-bottom: 48px;
    p {
      margin: 0;
      font-size: 13px;
      opacity: 1;
      text-align: center;
    }
  }

  .name_group__box {
    display: flex;
    justify-content: space-between;
    padding-bottom: 32px;
    .name__box {
      h3 {
        font-size: 17px;
        font-family: "aAutoSignature";
      }

      p {
        font-size: 15px;
        text-align: center;
      }
    }
  }

  .img__box {
    width: 100%;
    height: 500px;
    overflow: hidden;
    margin: 0 auto;

    .img-party {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .loc__box {
    padding-top: 32px;

    p {
      font-size: 15px;
    }
  }
`;

const InvBGMBlock = styled.div`
  padding-top: 12px;
  padding-right: 12px;
  display: flex;
  justify-content: right;
  color: #757575;
`;

const InvCover = () => {
  return (
    <>
      <InvBGMBlock>
        <VolumeOffIcon />
      </InvBGMBlock>
      <InvCoverBlock>
        <div className="intro__box">
          <p>2023 END-OF-YEAR PARTY</p>
        </div>
        <div className="name_group__box">
          {nameText.map((name: any, key: any) => (
            <div key={key}>
              <div className="name__box">
                <h3>{name.english}</h3>
                <p>{name.korean}</p>
              </div>
            </div>
          ))}
        </div>
        <div className="img__box">
          <img className="img_party" alt="" src={party} />
        </div>
        <div className="loc__box">
          <p>2023 12 29   7</p>
          <p> 1  </p>
        </div>
      </InvCoverBlock>
    </>
  );
};

export default InvCover;
