import React from "react";
import styled from "styled-components";
import LinkIcon from "@mui/icons-material/Link";
import ChatBubbleIcon from "@mui/icons-material/ChatBubble";

const InvFooter = () => {
  const InvFooterBlock = styled.div`
    padding-top: 60px;

    .share__box {
      padding-left: 10px;
      padding-right: 10px;
      height: 86px;
      background-color: #dfd3d3b3;
      display: flex;
      justify-content: space-evenly;

      .share__box__item {
        display: flex;
        flex-direction: column;
        width: 100px;
        justify-content: center;
        align-items: center;
        font-size: 13px;
        cursor: pointer;
      }
    }

    .text {
      padding-top: 60px;
      padding-bottom: 60px;
      font-size: 0.75rem;
      text-align: center;

      .number {
        text-decoration: none;
        color: rgba(26, 26, 26, 0.671);
      }
    }
  `;

  const handleCopyClipBoard = () => {
    try {
      navigator.clipboard.writeText(window.location.href);
      alert(" .");
    } catch (error) {
      alert("  .");
    }
  };

  return (
    <InvFooterBlock>
      <div className="share__box">
        <div className="share__box__item">
          <img src="" alt="" className="kakao" />
          <ChatBubbleIcon sx={{ color: "#000000" }} />
          <p> </p>
        </div>
        <div className="share__box__item" onClick={() => handleCopyClipBoard()}>
          <LinkIcon sx={{ color: "#000000" }} />
          <p> </p>
        </div>
      </div>
      <div className="text">
        Copyright 2023.
        <a href="tel:010-6529-5155" className="number">
          
        </a>
        All rights reserved.
      </div>
    </InvFooterBlock>
  );
};

export default InvFooter;
