import React from "react";
import styled from "styled-components";
import rose from "../assets/img/rose.png";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

const InvAccount = () => {
  const InvAccountBlock = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 60px;

    .img__box {
      padding-top: 100px;
      width: 100%;
      height: 100px;
      overflow: hidden;
      margin: 0 auto;
      text-align: center;

      .img__rose {
        width: 40px;
        height: 40px;
        object-fit: fill;
      }
    }
    .text__box {
      text-align: center;
      padding-bottom: 60px;

      .desc {
        font-size: 12px;
      }

      .title {
        font-size: 16px;
        padding-bottom: 48px;
      }
    }

    .account__title {
      color: #757575;
      font-size: 15px;
    }

    .account__desc {
      color: #757575;
      font-size: 14px;
    }

    .account__btn {
      width: 85px;

      .btn__copy {
        margin-top: 8px;
        width: 85px;
        height: 28px;
        background-color: #eb9778;
        border-radius: 4px;
        color: #ffffff;
        font-size: 13px;
        text-align: center;
        line-height: 28px;
      }

      .btn__copy {
        margin-top: 8px;
        width: 85px;
        height: 28px;
        background-color: #eb9778;
        border-radius: 4px;
        color: #ffffff;
        font-size: 13px;
        text-align: center;
        line-height: 28px;
        cursor: pointer;
      }
    }
  `;

  const handleCopyClipBoard = (text: string) => {
    try {
      navigator.clipboard.writeText(text);
      alert(" .");
    } catch (error) {
      alert("  .");
    }
  };

  return (
    <InvAccountBlock>
      <div className="img__box">
        <img className="img__rose" alt="" src={rose} />
      </div>
      <div className="text__box">
        <p className="title">  </p>
        <div className="desc">
          <p>   </p>
          <p> .</p>
          <p>   . </p>
        </div>
      </div>
      <div className="account__box">
        <Accordion
          disableGutters
          sx={{
            backgroundColor: "#dfd3d3b3",
            width: "380px",
            height: "48px",
            borderRadius: "4px",
            boxShadow: "none",
          }}
        >
          <AccordionSummary
            expandIcon={<ExpandMoreIcon sx={{ color: "#ffffff" }} />}
            aria-controls="panel1a-content"
            id="panel1a-header"
            sx={{
              height: "32px",
              textAlign: "center",
            }}
          >
            <p className="account__title"> </p>
          </AccordionSummary>
          <AccordionDetails
            sx={{
              backgroundColor: "#DFD3D34d",
              display: "flex",
              justifyContent: "space-between",
            }}
          >
            <div className="account__desc">
              <p> 1002853002188</p>
              <p></p>
            </div>
            <div className="account__btn">
              <div
                className="btn__copy"
                onClick={() => handleCopyClipBoard("1002853002188")}
              >
                
              </div>
              <div className="btn__kakao"></div>
            </div>
          </AccordionDetails>
        </Accordion>
      </div>
    </InvAccountBlock>
  );
};

export default InvAccount;
