import React from "react";
import Calendar from "react-calendar";
import styled from "styled-components";
import "react-calendar/dist/Calendar.css";

const InvCalender = () => {
  const specificDate = new Date("2023-12-29");
  //    Date  
  const today = new Date();
  //      ( )
  const timeDifference: any = specificDate.getTime() - today.getTime();
  //   
  const daysDifference = Math.ceil(timeDifference / (1000 * 60 * 60 * 24));

  const InvCalenderBlock = styled.div`
    padding-top: 60px;
    padding-left: 32px;
    padding-right: 32px;

    .text__box {
      letter-spacing: 0.2em;
      padding-bottom: 24px;
    }

    hr {
      background: #d3d3d3;
      height: 1px;
      border: 0;
    }

    .calender__box {
      padding-top: 12px;
      padding-bottom: 12px;

      .react-calendar {
        border: none;
        font-family: "RIDIBatang";
        pointer-events: none;
      }
      .react-calendar__month-view__weekdays {
        text-transform: none;
      }

      .react-calendar__month-view__days__day--weekend {
        color: #757575;
      }
      .react-calendar__month-view__weekdays__weekday abbr {
        text-decoration: none;
      }
      .react-calendar__month-view__days__day {
        color: #757575;
      }

      .react-calendar__tile--now {
        background: none;
      }

      .react-calendar__tile--active {
        background: #fadce2;
        border-radius: 50%;
      }
    }

    .alert {
      padding-top: 24px;
      text-align: center;
      font-size: 12px;
    }
  `;

  return (
    <InvCalenderBlock>
      <div className="text__box">
        <p></p>
        <p></p>
      </div>
      <hr />
      <div className="calender__box">
        <Calendar
          goToRangeStartOnSelect={false}
          showNavigation={false}
          showNeighboringMonth={false}
          locale="en"
          value="2023-12-29"
        />
      </div>
      <hr />
      <p className="alert"> {daysDifference} </p>
    </InvCalenderBlock>
  );
};

export default InvCalender;
